
#todoView {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background-color: rgba(0, 0, 0, 0.3);
  overflow-y: auto;
}
#todoView .layout {
    width: 600px;
    margin: 0 auto;
    padding: 16px;
}
#todoView h2 {
    font-size: 24px;
}
#todoView .list-enter-active, #todoView .list-leave-active {
    -webkit-transition: all .5s;
    transition: all .5s;
}
#todoView .list-enter, #todoView .list-leave-to {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
}
#todoView .list-move {
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}
#todoView li {
    height: 32px;
    line-height: 32px;
    background: #fff;
    position: relative;
    margin-bottom: 10px;
    padding-left: 40px;
    border-radius: 3px;
    border-left: 5px solid #629A9C;
    -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07);
            box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07);
    overflow: hidden;
}
#todoView li input {
      position: absolute;
      top: 5px;
      left: 10px;
      width: 22px;
      height: 22px;
      cursor: pointer;
}
#todoView li p {
      margin: 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
#todoView li p .title {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        margin-right: 5px;
        border-radius: 3px;
        height: 32px;
        overflow: hidden;
        cursor: pointer;
}
#todoView li p .title:hover {
          background-color: #444;
          color: #fff;
}
#todoView li p .btn {
        margin: 0;
        position: relative;
}



