
.panel {
  border-radius: 8px;
  overflow: hidden;
}
.panel .title {
    text-align: center;
    background-color: #12b7f5;
    min-height: 32px;
    max-height: 96px;
    overflow: auto;
    line-height: 32px;
    font-size: 16px;
    color: #fff;
}
.panel .detail {
    background-color: #fff;
    height: 100px;
    overflow: auto;
    color: #333;
    padding: 8px;
}
.panel .info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: 8px 16px;
    background-color: #12b7f5;
}



