
#signInAndSignUp h1 {
  margin-bottom: 80px;
  font-size: 48px;
  text-align: center;
  color: #12b7f5;
}
#signInAndSignUp .loginMethods {
  margin-bottom: 36px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#signInAndSignUp .loginMethods .btn {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    border-radius: 0;
    background-color: #353640;
    color: #fff;
}
#signInAndSignUp .loginMethods .btn:hover {
      background-color: #12b7f5;
}
#signInAndSignUp .loginMethods .btn.active {
      border-bottom: 2px solid #12b7f5;
}
#signInAndSignUp .formRow {
  margin-top: 24px;
}
#signInAndSignUp .formRow span {
    color: #fff;
}
#signInAndSignUp .formAction {
  margin-top: 16px;
  text-align: center;
}



