
#banner {
  position: fixed;
  z-index: -1;
  left: 396px;
  right: 0;
  top: 0;
  bottom: 0;
  background: url("../assets/banner.jpg") no-repeat;
  background-size: cover;
}
#banner h2 {
    color: #353d40;
    margin-left: 50px;
}
#banner .slogan {
    position: absolute;
    top: 140px;
    left: 80px;
}
#banner .slogan b {
      font-weight: 700;
      font-size: 50px;
      color: #1c1e1f;
      letter-spacing: 5px;
}
#banner .slogan span {
      font-size: 18px;
      color: #353d40;
      line-height: 50px;
      padding-left: 5px;
}
#banner.opacity {
    opacity: .2;
}



